<template>
  <v-card class="full text-center pa-6" dark>

    <h1 class="mb-6" v-if="item" style="font-size: 72px;">🎉 New Deposit From <b>"{{ item.user.displayName }}"</b> 🎉</h1>
    <h1 class="green--text font-weight-bold" v-if="item" style="font-size: 180px;">{{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}</h1>
  
    <v-simple-table v-if="item" class="mt-6" style="width: 600px">
      <tbody>
        
        <tr>
          <td> Account name </td>
          <td>{{ item.name }}</td>
        </tr>
        <tr>
          <td> Country </td>
          <td>{{ country }}</td>
        </tr>
        <tr>
          <td> Affiliate </td>
          <td>{{ item.affiliate.name }}</td>
        </tr>
        <tr>
          <td> Payment Method </td>
          <td>{{ item.payment_method.name }}</td>
        </tr>
      </tbody>
  </v-simple-table>
    <h1 v-if="!item"> No Deposit Yet!! </h1>
    <h1 v-if="!item" style="font-size:200px; opacity:0.7">🙁</h1>
  </v-card>
</template>
<script>
import {mapState} from 'vuex' 
import countries from './../country'
var audio = new Audio('/Ding-ding-sound.mp3');
export default {
  name: 'screen',
  data () {
    return {
      countries,
      item: null
      
    }
  },
  methods: {
    notefy (data) {
      this.item = data.item
      const msg = `${data.user} made (${new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(data.amount)})`
      // Let's check if the browser supports notifications
      if (!("Notification" in window)) {
        console.log("This browser does not support desktop notification");
      }

      // Let's check whether notification permissions have alredy been granted
      else if (Notification.permission === "granted") {
        // If it's okay let's create a notification
        var notification = new Notification(msg);
        console.log(notification);
      }

      // Otherwise, we need to ask the user for permission
      else if (Notification.permission !== 'denied' || Notification.permission === "default") {
        Notification.requestPermission(function (permission) {
          // If the user accepts, let's create a notification
          if (permission === "granted") {
            var notification = new Notification(msg);
            console.log(notification);
          }
        });
      }
          audio.play();

    }
  },
  computed: {
    ...mapState({
      io: state => state.api.io  
    }),
    country () {
      if (this.item && this.item.country) {
        const item = this.countries.filter(e => e.code == this.item.country) 
        if (item.length)
          return item[0].name
      }
      return ''
    }
    
  },
  async created () {
    
    
    this.io.on('notefy', this.notefy)
   
  },
  destroyed () {
    this.io.off('notefy', this.notefy)
    
  }
}
 
</script>

<style scoped>
.full {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
table tr td {
  font-size: 36px;
  padding: 13px;
}
</style>

